import React from 'react';
import PropTypes from 'prop-types';

import { PopupMenu } from '../';

const SortPopup = ({ sortBy, onSelect }) => {
  const items = [
    { value: 'rating', label: '–ø–æ–ø—É–ª—è—Ä–Ω–æ–º—É' },
    { value: 'price', label: '—Ü–µ–Ω–µ' },
    { value: 'name', label: '–∞–ª—Ñ–∞–≤–∏—Ç—É' },
  ];
  const selected = items.find(obj => obj.value === sortBy);
  return (
    <div className="sort">
      <PopupMenu onClick={onSelect} activeItem={selected.value} items={items}>
        <div className="sort__label">
          <b>–°–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ –ø–æ:</b>
          <span>{selected.label}</span>
        </div>
      </PopupMenu>
    </div>
  );
};

SortPopup.propTypes = {
  sortBy: PropTypes.string,
  onSelect: PropTypes.func,
};

SortPopup.defaultProps = {
  sortBy: 'rating',
};

export default SortPopup;
